import { useState } from "react";
import { useDaumPostcodePopup } from "react-daum-postcode";
import { useContext } from "react";
import { AuthContext } from "../../store/AuthContext";

const PaymentPage = () => {
  const open = useDaumPostcodePopup();

  const { cart, addOrder } = useContext(AuthContext);
  let totPrice = 0;

  if (cart) {
    totPrice = cart.reduce((acc, cur) => acc + cur.price * cur.quantity, 0);
  }

  const [isSame, setIsSame] = useState(false);

  const [name, setName] = useState("");
  const [mobile, setMobile] = useState("");
  const [name2, setName2] = useState("");
  const [mobile2, setMobile2] = useState("");

  const [address, setAddress] = useState("");
  const [postcode, setPostcode] = useState("");

  const checkboxHandler = () => {
    if (isSame) {
      setIsSame(false);
      setName2("");
      setMobile2("");
    } else {
      setIsSame(true);

      setName2(name);
      setMobile2(mobile);
    }
  };

  const handleComplete = (data) => {
    let fullAddress = data.address;
    let extraAddress = "";

    if (data.addressType === "R") {
      if (data.bname !== "") {
        extraAddress += data.bname;
      }
      if (data.buildingName !== "") {
        extraAddress +=
          extraAddress !== "" ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress !== "" ? ` (${extraAddress})` : "";
    }

    setPostcode(data.zonecode);
    setAddress(fullAddress);
    // console.log(fullAddress); // e.g. '  2 20 (1)'
  };

  const handleClick = () => {
    open({ onComplete: handleComplete });
  };

  const orderHandler = () => {
    addOrder();
  };
  return (
    <div className="pageWrapper">
      <div className="payment">
        <div
          style={{
            width: "100%",
            display: "flex",
            borderBottom: "1px solid #d2d2d7",
          }}
        >
          <div style={{ width: "50%", fontSize: 24, fontWeight: 500 }}>
            
          </div>
          <div style={{ width: "50%", display: "flex", justifyContent: "end" }}>
               : {totPrice + 3000} (3000 )
          </div>
        </div>
        <div style={{ marginTop: 16, width: "100%", display: "flex" }}>
          <div style={{ width: "49%" }}>
            <h5> </h5>
            <input
              value={name}
              type="text"
              name="full"
              placeholder=""
              onChange={(e) => setName(e.target.value)}
            />
            <input
              value={mobile}
              type="text"
              name="mobile"
              placeholder=" "
              onChange={(e) => setMobile(e.target.value)}
            />
            <div>
              <input
                onChange={checkboxHandler}
                checked={isSame}
                type="checkbox"
                id="sameInfo"
                name="sameInfo"
              />
              <label htmlFor="sameInfo">   .</label>
            </div>
          </div>
          <div style={{ width: "2%" }} />
          <div style={{ width: "49%" }}>
            <h5> </h5>
            <input
              value={name2}
              onChange={(e) => setName2(e.target.value)}
              type="text"
              name="full"
              placeholder=""
            />
            <input
              value={mobile2}
              onChange={(e) => setMobile2(e.target.value)}
              type="text"
              name="mobile"
              placeholder=" "
            />

            <h5> </h5>
            <input
              onClick={handleClick}
              type="text"
              readOnly
              placeholder=""
              value={address}
            />
            <input type="text" name="address2" placeholder=" " />
            <input
              value={postcode}
              type="text"
              readOnly
              placeholder=""
            />

            <h5></h5>
            <select>
              <option>  </option>
              <option> </option>
            </select>

            <button
              onClick={orderHandler}
              style={{ width: "100%", marginTop: 10 }}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PaymentPage;
