import { useState, useContext } from "react";
import { Alert } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../store/AuthContext";

const ProductInfo = (props) => {
  const navigate = useNavigate();
  const { item } = props;
  const [count, setCount] = useState(1);
  const { isAuth, addCart } = useContext(AuthContext);
  const [show, setShow] = useState(false);

  const quantityButtonHandler = (type) => {
    if (type === "plus") {
      setCount((prev) => prev + 1);
    } else {
      if (count === 1) return;
      setCount((prev) => prev - 1);
    }
  };

  const addCartHandler = () => {
    if (!isAuth) {
      alert("  ");
      navigate("/login");
    } else {
      const new_item = {
        ...item,
        quantity: count,
      };
      addCart(new_item);
      setShow(true);
      setTimeout(() => setShow(false), 2000);
    }
  };

  const buyHandler = () => {
    if (!isAuth) {
      alert("  ");
      navigate("/login");
    } else {
      const new_item = {
        ...item,
        quantity: count,
      };
      addCart(new_item);
      navigate("/user/cart");
    }
  };
  return (
    <div>
      {show && (
        <Alert variant="info">
          <Alert.Heading>  .</Alert.Heading>
          <p> .</p>
        </Alert>
      )}
      <p style={{ color: "#bf4800", marginBottom: 0 }}>New</p>
      <h1 style={{ marginBottom: 20 }}>{item.name} </h1>
      <h5>
        {item.summary}    {item.price}
      </h5>

      <div className="quantity">
        <p style={{ fontWeight: 600, marginBottom: 5 }}></p>
        <button
          className="plus-btn"
          type="button"
          name="button"
          onClick={() => quantityButtonHandler("plus")}
        >
          +
        </button>
        <input type="text" readOnly name="name" value={count} />
        <button
          className="minus-btn"
          type="button"
          name="button"
          onClick={() => quantityButtonHandler("minus")}
        >
          -
        </button>
      </div>

      <br />
      <br />
      <h3>  : {item.price * count}</h3>
      <div className="product-info-action" onClick={addCartHandler}>
         
      </div>
      <div className="product-info-action" onClick={buyHandler}>
         
      </div>
    </div>
  );
};

export default ProductInfo;
